// import http from "../services/DiaryDataService";
import http from "../http-common";

class DiaryDataService {
    // Î™®Îì† Î∂ÄÏÑúÏ†ïÎ≥¥ Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò
    getAll(id) {
        // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/diary")
        return http.get(`/diary/all/${id}`); 
    }

    // Î∂ÄÏÑúÎ≤àÌò∏Î°ú Ï°∞Ìöå ÏöîÏ≤≠ Ìï®Ïàò
    // get Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @GetMapping("/api/diary/{no}"), @PathVariable
    get(dno) {
        return http.get(`/diary/${dno}`)
    }

    // Î∂ÄÏÑúÏ†ïÎ≥¥ ÏÉùÏÑ±(insert) ÏöîÏ≤≠ Ìï®Ïàò
    // post Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @PostMapping("/api/diary"), @RequestBody
    create(data) {
        console.log(data);
        return http.post("/diary", data);
    }

    // Î∂ÄÏÑúÏ†ïÎ≥¥ ÏàòÏ†ï(update) ÏöîÏ≤≠ Ìï®Ïàò
    // put Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @PutMapping("/api/diary/{no}"), @RequestBody
    update(dno, data) {
        alert("ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏàòÏ†ïÎêòÏóàÏäµÎãàÎã§.");
        return http.put(`/diary/${dno}`, data);
    }

    // Î∂ÄÏÑúÏ†ïÎ≥¥ ÏÇ≠Ï†ú(delete) ÏöîÏ≤≠ Ìï®Ïàò
    // delete Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @DeleteMapping("/api/diary/deletion/{no}")
    //                        , @PathVariable  
    delete(dno) {
        alert("ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.");
        return http.delete(`/diary/deletion/${dno}`);
    }

    // Î∂ÄÏÑúÏ†ïÎ≥¥ Ï†ÑÏ≤¥ ÏÇ≠Ï†ú ÏöîÏ≤≠ Ìï®Ïàò
    // delete Î∞©Ïãù ÌÜµÏã† ÏöîÏ≤≠ -> @DeleteMapping("/api/diary/all")
    deleteAll() {
        return http.delete("/diary/all")
    }


}

export default new DiaryDataService();